import React from "react";
import "./Detail.css"

class Detail extends React.Component {
    componentDidMount(){
        const {location, history} = this.props;
        if(location.state === undefined){
            history.push("/"); //원하는 주소로 보내버림
        }
    }
   render(){
       const{location} = this.props;
       if(location.state){
           return (
            <div class="movieDetail">
                <img src={location.state.poster} title={location.state.title} alt={location.state.title} />
                <div className="movie_Detaildata">
                <h3 className="movie_Detailtitle">{location.state.title}</h3>
                <h5 className="movie_Detailyear">{location.state.year}</h5>
                <ul className="Detailgenres">
                    {location.state.genres.map((genre, index) => (
                        <li key={index} className="Detailgenres_genre">{genre}</li>
                    ))}
                </ul>
                <p className="movie_Detailsummary">{location.state.summary}</p>
                </div>
            </div>
            )
       } else {
           return null;
       }
   } 
}

export default Detail;